import { useSelector } from "react-redux";
import { useGetUserQuery } from "../../redux/reducers/usersApi";
import * as S from "./ProfilePage.style";
import { prepareDate } from "./Helpers/prepareDate";
import { Repositories } from "../Repositories/Repositories";
import loader from "./.gif";

export const ProfilePage = () => {
  const login = useSelector(
    (state) => state.search.searchParams.selectedUserUrl
  );

  const { data: user, isLoading: userLoading } = useGetUserQuery(login);

  if (userLoading)
    return (
      <S.ProfileLoading>
        {" "}
        <img src={loader} alt=". ..." />
      </S.ProfileLoading>
    );

  return (
    <S.ProfileContainer>
      <S.ProfileContent>
        <S.ProfileSettings>
          <S.ProfileLeft>
            <S.ProfileImgContainer>
              <S.ProfileImg src={user.avatar_url} alt="user" />
            </S.ProfileImgContainer>
            <S.UserName>: {user.login}</S.UserName>
          </S.ProfileLeft>
          <S.ProfileRight>
            <S.UserInfo>: {user.name || " "}</S.UserInfo>
            <S.UserInfo>: {user.blog || " "}</S.UserInfo>
            <S.UserInfo>
              : {user.location || " "}
            </S.UserInfo>
            <S.UserInfo>: {user.company || " "}</S.UserInfo>
            <S.UserInfo>: {user.email || " "}</S.UserInfo>
            <S.UserInfo>: {user.followers || ""}</S.UserInfo>
            <S.UserInfo>() : {user.following || ""}</S.UserInfo>
            <S.UserInfo>
               : {prepareDate(user.created_at) || ""}
            </S.UserInfo>
          </S.ProfileRight>
        </S.ProfileSettings>
      </S.ProfileContent>
      <S.ProfileTitle>
          : {user.public_repos}
      </S.ProfileTitle>
      <Repositories login={login} count={user.public_repos} />
    </S.ProfileContainer>
  );
};
